<template>
    <div>
    <!--  -->
    <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb> 
    <!--  -->
    <el-card >
        <el-row>
            <el-col :span="8">
                <el-input placeholder="">
                    <el-button icon="el-icon-search" slot="append"></el-button>
                </el-input>
            </el-col>
        </el-row>
        <!--  -->
        <el-table :data="orderList" border stripe >
            <el-table-column label="#"  type="index"> </el-table-column>
            <el-table-column label=""  prop="order_number"> </el-table-column>
            <el-table-column label=""  prop="order_price"> </el-table-column>
            <el-table-column label=""  prop="pay_status">
                <template slot-scope="scope">
                    <el-tag type="success" v-if="scope.row.pay_status==='1'"> </el-tag>
                    <el-tag type="danger" v-else> </el-tag>
                </template>  
            </el-table-column>
            <el-table-column label=""  prop="is_send"> </el-table-column>
            <el-table-column label=""  prop="create_time"> 
                <template slot-scope="scope">
                    {{ scope.row.create_time | dateForamt }}
                </template>
            </el-table-column>
            <el-table-column label="" >
            <template slot-scope="scope">
                <el-button icon="el-icon-edit" size="mini" type="primary" @click="showbox"></el-button>
                <el-button icon="el-icon-location" size="mini" type="success" @click="showProgressDialog"></el-button>

            </template>
            </el-table-column>
            
        </el-table>
      </el-card>
        <!--  -->
        <el-pagination  @size-change="handleSizeChange" @current-change="handleCurrentChange"    :current-page="queryInfo.pagenum"   :page-sizes="[5, 10, 15]"      :page-size="queryInfo.pagesize"       layout="total, sizes, prev, pager, next, jumper"       :total="total">
        </el-pagination>
            <!--  -->
        <el-dialog title="" :visible.sync="addressVisible" width="50%" @close="addressClosed" >
            <el-form :model="addressForm" :rules="addressFormRules" ref="addressFormRef" label-width="100px" >
                <el-form-item label="/" prop="address1">
                    <el-cascader    v-model="addressForm.address1"    :options="cityData"  :props="{ expandTrigger: 'hover'  }"    ></el-cascader>

                </el-form-item>
                <el-form-item label="" prop="address2">
                    <el-input v-model="addressForm.address2"></el-input>
                  </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="addressVisible = false"> </el-button>
                <!--   -->
                <el-button type="primary"   @click="addressVisible = false"> </el-button>
            </span>
        </el-dialog>
     <!--  -->
     <el-dialog title="" :visible.sync="progressVisible" width="50%" @close="progressClosed">
        <el-timeline >
            <el-timeline-item
              v-for="(activity, index) in progressInfo" :key="index" :timestamp="activity.time">
              {{activity.context}}
            </el-timeline-item>
          </el-timeline> 
    </el-dialog>
    </div>
</template>

<script>
import cityData from './citydata.js'
export default {

    data() {
        return {
            queryInfo:{
                query:'',
                pagenum:1,
                pagesize:10,
            },
            total:0,
            orderList:[],
            addressVisible:false,
            addressForm:{
                // /
                address1:[],
                // 
                address2:''
            },
            addressFormRules:{
                address1:[
                    { required: true, message: '/', trigger: 'blur' },
                ],
                address2:[
                    { required: true, message: '', trigger: 'blur' },
                ],
            },
            cityData,
            progressVisible:false,
            progressInfo: [
                    {
                    "time": "2018-05-10 09:39:00",
                    "ftime": "2018-05-10 09:39:00",
                    "context": ",,",
                    "location": ""
                    },
                    {
                    "time": "2018-05-10 08:23:00",
                    "ftime": "2018-05-10 08:23:00",
                    "context": "[]  95338",
                    "location": ""
                    },
                    {
                    "time": "2018-05-10 07:32:00",
                    "ftime": "2018-05-10 07:32:00",
                    "context": " []",
                    "location": ""
                    },
                    {
                    "time": "2018-05-10 02:03:00",
                    "ftime": "2018-05-10 02:03:00",
                    "context": "[], []",
                    "location": ""
                    },
                    {
                    "time": "2018-05-09 23:05:00",
                    "ftime": "2018-05-09 23:05:00",
                    "context": " []",
                    "location": ""
                    },
                    {
                    "time": "2018-05-09 21:21:00",
                    "ftime": "2018-05-09 21:21:00",
                    "context": "[], []",
                    "location": ""
                    },
                    {
                    "time": "2018-05-09 13:07:00",
                    "ftime": "2018-05-09 13:07:00",
                    "context": " ",
                    "location": ""
                    },
                    {
                    "time": "2018-05-09 12:25:03",
                    "ftime": "2018-05-09 12:25:03",
                    "context": "",
                    "location": ""
                    },
                    {
                    "time": "2018-05-09 12:22:24",
                    "ftime": "2018-05-09 12:22:24",
                    "context": "HLA",
                    "location": ""
                    },
                    {
                    "time": "2018-05-08 21:36:04",
                    "ftime": "2018-05-08 21:36:04",
                    "context": "",
                    "location": ""
                    }
            ],

        };
    },
    methods: {
        // 
        async getOrderList(){ 
           const {data:res}= await this.$http.get('orders',{params:this.queryInfo})
           if(res.meta.status!==200) return this.$message.error('')
            this.total=res.data.total
            this.orderList=res.data.goods
            console.log(res)

        },
        handleCurrentChange(newPage){
            console.log(newPage)
            this.queryInfo.pagenum=newPage
            this.getOrderList()
        },
        handleSizeChange(newSize){
            console.log(newSize)
            this.queryInfo.pagesize=newSize
            this.getOrderList()
        },
        // 
        showbox(){
            this.addressVisible=true
        },
        addressClosed(){
            this.$refs.addressFormRef.resetFields()
        },
        async showProgressDialog(){
            // const {data:res }= await this.$http.get(`/kuaidi/1106975712662`)
            // if(res.meta.status!==200 ) return this.$message.error('')
            //   data
            // this.progressInfo=res.data
            console.log(this.progressInfo)
            this.progressVisible=true

        },
        progressClosed(){

        }
    },
    created(){
        this.getOrderList()
    }
};
</script>

<style lang="less" scoped>
.el-cascader{
    width: 100%;
}
</style>